import React from "react";
import {SuccessIcon, CloseIcon, InfoIcon, WarningIcon, ErrorIcon} from "./NoficationIcon";

const NotificationIconMapping = {
  success: <SuccessIcon />,
  info: <InfoIcon />,
  warning: <WarningIcon />,
  error: <ErrorIcon />
}

interface NotificationProps {
  type: "success" | "error" | "info" | "warning"; // "rgb(34 197 94)", "rgb(239 68 68)", "rgb(14 165 233)", "rgb(234 179 8)".
  message: string;
  title: string | React.ReactNode;
  duration: number;
  visible: boolean;
  onClose: () => void;
}

export const Notification = ({
  type = "success",
  message,
  title,
  duration = 0,
  visible = false,
  onClose,
}: NotificationProps) => {
  return (
    <div className="toast" style={{'--toast-color': 'blue'} as React.CSSProperties}>
      <div className="toast__icon">
        <SuccessIcon />
      </div>
      <div className="toast__body">
        <h3>Success!</h3>
        <p>Bạn đã đúng</p>
      </div>
      <div className="toast__close">
        <CloseIcon />
      </div>
    </div>
  );
};
